body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
}

h1.heading {
  text-align: center;
  margin-top: 20px;
}

.orderedLineCanvas {
  display: block;
  background-color: #e7eff7;
  margin: 5px auto;
  border: 1px solid #000;
  border-radius: 20px;
  width: 96vw;
  height: calc(100vh - 11em); /* Adjusting for margins and h1 height */
}

.addItem {
  padding: 5px !important;
}

.addItemBar {
  display: flex;
  justify-content: center;
}
